  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#message {
  text-align: center;
  color: orange;
  opacity: 0;
  background: #000;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 2em;
  position: fixed;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#score {
  color: orange;
  z-index: 1;
  background: #000c;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 20px;
