  position: fixed;
  top: 10px;
  right: 10px;
}

body {
  font-family: Arial, sans-serif;
  overflow: hidden;
}

#game-over {
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: #00000080;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

#game-over-content {
  text-align: center;
  max-width: 90%;
  background: orange;
  border-radius: 10px;
  padding: 20px;
  position: absolute;
